body {
  background: #2c3E50;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

footer {
  background: rgba(0, 0, 0, 0.548);
  border: solid 5px #0fa36b;
  border-radius: 10px;

}
header {
  background: black;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px;
  border-radius: 10px;
  border: #fff 1px solid;
}
header img {
  width: 10vmax;
}
header span {
  font-size: 30px;
  font-family: monospace;
  display: flex;;
  font-weight: bold;
}

.countdown {
  padding: 10px;
  display: flex;
  flex-flow: column wrap;
  font-family: monospace;
}
.countdown-segment {
  padding: 10px;
  display: flex;
  justify-content: center;
  font-size: 25px;
  font-weight: bolder;
}
.countdown-init-message{
  text-align: start;
  font-size: 15px;
}
.countdown-end-message {
  text-align: end;
  font-size: 15px;
}
.countdown span {
  padding: 3px;
  color:#0fa36b;
}

.main-section {
  background: transparent;
  max-width: 600px;
  width: 90%;
  height: 500px;
  margin: 30px auto;
}

.clock-holder {
  width: 100%;
  background: #fff;
  margin: 30px auto;
  position: relative;
}

.stopwatch {
  padding: 20px 0;
  text-align: center;
  min-height: 195px;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
}

.stopwatch span {
  background: #0fa36b;
  color:#fff;
  display: inline-block;
  font-family: monospace;
  font-size: 22px;
  padding:20px;
  border-radius:5px;
  width:140px;
  text-align: center;
  font-size: 25px;
}

.stopwatch .stopwatch-btn {
  background: #fff;
  margin-top: 20px;
  margin-left: 5px;
  border:none;
  padding:12px 20px;
  font-size: 16px;
  text-transform: uppercase;
  width: 150px;
  transition: background 1s;
  cursor: pointer;
}

.stopwatch .stopwatch-btn-red{
  border:2px solid #c85454;
  color: #c85454
}
.stopwatch .stopwatch-btn-yellow{
  border:2px solid #ffc900;
  color: #ffc900
}
.stopwatch .stopwatch-btn-green{
  border:2px solid #0fa36b;
  color: #0fa36b
}

.stopwatch .stopwatch-btn-red:hover{
  background: #c85454;
  border:2px solid #c85454;
  color:#fff;
}
.stopwatch .stopwatch-btn-yellow:hover{
  background: #ffc900;
  border:2px solid #ffc900;
  color:#fff;
}
.stopwatch .stopwatch-btn-green:hover{
  background: #0fa36b;
  border:2px solid #0fa36b;
  color:#fff;
}

.input-btn {
  border: none;
  background-color: grey;
  margin-bottom: 20px;
  height: 30px;
  width: 50px;
  text-align: center;
  border-radius: 5px;
  font-size:20px;
  font-weight: bold;
}

.set-time-btn {
  background: transparent;
  margin-top: 20px;
  margin: 5px;
  border:none;
  padding:10px 15px;
  font-size: 16px;
  text-transform: uppercase;
  width: 75px;
  transition: background 1s;
  cursor: pointer;
  text-align: center;
}

.selected {
  background: black;
}

.big{
  background: #fff;
  margin-top: 20px;
  margin-left: 5px;
  border:none;
  padding:12px 20px;
  font-size: 16px;
  text-transform: uppercase;
  width: 250px;
  transition: background 1s;
  cursor: pointer;
}

